.nav {
  &__list {
    margin: 0;
    padding: 0;
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 20px;
  }

  &__link {
    position: relative;
    display: block;
    padding: 22.5px 0;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
    color: $color-primary-accent-black;
    text-transform: uppercase;
    transition: color $duration-animation;

    &:hover {
      color: $color-primary-accent-blue;
    }

    &--is-active {
      color: $color-primary-accent-blue;
    }

    &--is-active::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      background-color: $color-primary-accent-blue;
      border-radius: 8px;
    }
  }
}
